/* =========================
   GOOGLE FONTS
========================= */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Cinzel:wght@500;600&family=Amiri:wght@400;700&family=Orbitron:wght@500;600&display=swap');

/* =========================
   PREMIUM ISLAMIC UI RESET
========================= */
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Poppins", "Segoe UI", sans-serif;
  background:
    linear-gradient(
      rgba(6, 20, 28, 0.85),
      rgba(6, 20, 28, 0.85)
    ),
    url("https://i.ibb.co/x87StfB2/madina.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  color: #e5e7eb;
}

/* =========================
   APP WRAPPER
========================= */
.app {
  max-width: 420px;
  margin: 0 auto;
  padding: 20px 16px;
}

/* =========================
   DATE HEADING
========================= */
.app h2 {
  text-align: center;
  font-family: "Cinzel", serif;
  font-size: 1.35rem;
  font-weight: 600;
  letter-spacing: 1px;
  color: #facc15;
  margin-bottom: 6px;
}

/* =========================
   LIVE TIME
========================= */
.time {
  text-align: center;
  font-family: "Playfair Display", serif;
  font-size: 0.95rem;
  font-weight: 500;
  margin-bottom: 18px;
  color: #a7f3d0;
  font-size: xx-large;
}

/* =========================
   CARD (GLASS EFFECT)
========================= */
.card {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border-radius: 18px;
  padding: 16px;
  box-shadow:
    0 20px 40px rgba(0, 0, 0, 0.35),
    inset 0 0 0 1px rgba(255, 255, 255, 0.12);
}

/* =========================
   PRAYER ROW
========================= */
.prayer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 14px 14px;
  margin: 6px 0;
  border-radius: 14px;
  transition: all 0.35s ease;
}

/* Prayer Name */
.prayer span {
  font-family: "Amiri", serif;
  font-size: 1.05rem;
  font-weight: 700;
  letter-spacing: 0.4px;
}

/* Prayer Time */
.prayer strong {
  font-family: "Poppins", sans-serif;
  font-size: 1.05rem;
  font-weight: 700;
  letter-spacing: 0.6px;
}

/* =========================
   STATUS STYLES
========================= */

/* Past Prayer */
.prayer.past {
  background: linear-gradient(
    135deg,
    rgba(251, 50, 36, 0.712),
    rgba(238, 104, 104, 0.329)
  );
}

.prayer.past span,
.prayer.past strong {
  color: #f0ede1;
}

/* Current / Nearest Prayer */
.prayer.current {
  background: linear-gradient(
    135deg,
    rgb(0, 245, 90),
    rgba(3, 139, 53, 0.25)
  );
  box-shadow: 0 0 25px rgba(7, 236, 91, 0.6);
  transform: scale(1.02);
}

.prayer.current span {
  color: #ecfdf5;
}

.prayer.current strong {
  color: #bbf7d0;
}

/* Upcoming Prayer */
.prayer.upcoming {
  background: linear-gradient(
    135deg,
    rgba(255, 251, 0, 0.61),
    rgba(255, 251, 0, 0.301)
  );
}

.prayer.upcoming span,
.prayer.upcoming strong {
  color: #e0f2fe;
}

/* =========================
   HOVER EFFECT
========================= */
.prayer:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.35);
}

/* =========================
   NO DATA
========================= */
.nodata {
  text-align: center;
  margin-top: 24px;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  color: #f87171;
}

/* =========================
   DESKTOP
========================= */
@media (min-width: 768px) {
  .app {
    max-width: 520px;
  }

  .prayer span,
  .prayer strong {
    font-size: 1.1rem;
  }
}























/* ================= ROOT ================= */
:root {
  --calendar-topbar-height: 60px;
}

/* ================= BUTTONS ================= */
.calendar-btn {
   margin-top: 15px;
  padding: 10px 16px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  background: linear-gradient(135deg, #f5c542, #e0b000);
  color: #000;
  border: none;
  touch-action: manipulation;
}
.back-btn {
  margin-top: 4px;
  padding: 10px 16px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  background: linear-gradient(135deg, #f5c542, #e0b000);
  color: #000;
  border: none;
  touch-action: manipulation;
}

/* ================= STICKY TOP BAR ================= */
.calendar-topbar {
  position: sticky;
  top: 0;
  z-index: 1000;

  height: var(--calendar-topbar-height);
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 10px 14px;
  margin-bottom: 8px;

  background: rgba(10, 15, 25, 0.9);
  backdrop-filter: blur(10px);
}

/* Month select */
.month-select {
  padding: 8px 12px;
  border-radius: 8px;
  font-weight: 600;
  background: linear-gradient(135deg, #f5c542, #e0b000);
  color: #000;
  border: none;
  font-size: 16px;
}

/* ================= TABLE WRAPPER ================= */
.table-wrap {
  width: 100%;
  overflow-x: auto;
  overflow-y: auto;
  max-height: calc(100vh - var(--calendar-topbar-height) - 80px);
  -webkit-overflow-scrolling: touch;
}

/* ================= TABLE ================= */
.calendar-table {
  border-collapse: separate;
  border-spacing: 0;
  min-width: 900px;
  background: rgba(20, 20, 20, 0.55);
}

/* ================= CELLS ================= */
.calendar-table th {
  border: 1px solid #000000;
  padding: 10px 12px;
  text-align: center;
  white-space: nowrap;
  font-size: 14px;
  background: transparent;
}

.calendar-table td {
  border: 1px solid #ffffff;
  padding: 10px 12px;
  text-align: center;
  white-space: nowrap;
  font-size: 14px;
  background: transparent;
}

/* ================= ALTERNATE INNER DATA (EXCLUDING DAY COLUMN) ================= */
.calendar-table tbody tr:nth-child(odd) td:not(.day-cell) {
 background: rgba(255, 217, 0, 0.233);

}

.calendar-table tbody tr:nth-child(even) td:not(.day-cell) {
  background: rgba(255, 255, 255, 0.12);
}

/* ================= STICKY HEADER ROW ================= */
.calendar-table thead th {
  position: sticky;
  top: 0;
  background: #111;
  color: #fff;
  z-index: 3;
}

/* ================= STICKY FIRST COLUMN ================= */
.calendar-table .day-cell,
.calendar-table thead th:first-child {
  position: sticky;
  left: 0;
  background: #000;   /* stays original */
  color: #fff;
  font-weight: 600;
  z-index: 4;
}

/* Top-left cell above all */
.calendar-table thead th:first-child {
  z-index: 5;
}

/* ================= MOBILE TWEAKS ================= */
@media (max-width: 768px) {
  .calendar-btn {
    width: 100%;
    margin-top: 5px;
    margin-left: 0px;
    font-size: 15px;
  }

  .calendar-topbar {
    padding: 8px 10px;
  }

  .calendar-table th,
  .calendar-table td {
    padding: 8px 10px;
    font-size: 13px;
  }
}
































